body {
  background-color: #303030;
}

.App {
  text-align: center;
  color: #ffffff;
  background: #7e7e7e;
  max-width: 360px;
  margin: 0 auto;
  padding: 15px;
  border: 3px solid #2f90ff;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1 {
  text-shadow: 2px 1px #303030;
}

.Message {
  min-height: 50px;
}

.AudioList {
  cursor: pointer;
}

.AudioList div {
  padding: 5px;
  margin-top: 15px;
}

.AudioList div:hover {
  opacity: 0.5;
}

.audioCurrent {
  color: #2f90ff;
  background: #303030;
  margin: 0 10%;
  border: none;
  border-top-left-radius: 15px;
  border-bottom-right-radius: 15px;
}




